import {Carousel} from "react-bootstrap";
import firstImage from "../../../img/slide11.png";
import secondImage from "../../../img/slide2.png";
import thirdImage from "../../../img/slide3.png";
import fourthImage from "../../../img/slide4.png";
import React from "react";

export default function CustomCarousel() {
    return <Carousel
        indicators={true}
        prevIcon={<span aria-hidden="true" className="carousel__icon carousel__icon_left-arrow"/>}
        nextIcon={<span aria-hidden="true" className="carousel__icon carousel__icon_right-arrow"/>}
    >
        <Carousel.Item>
            <img className="carousel__img"
                 src={firstImage}
                 alt="First slide"
            />
            <Carousel.Caption className="carousel-caption text-left">
                <h3> </h3>
                <p>        ,   , 
                      .</p>
                <button className="carousel__button green"></button>
            </Carousel.Caption>
        </Carousel.Item>
        <Carousel.Item>
            <img className="carousel__img"
                 src={secondImage}
                 alt="Third slide"
            />
            <Carousel.Caption className="carousel-caption text-left">
                <h3></h3>
                <p>  </p>
                <button className="carousel__button blue"></button>
            </Carousel.Caption>
        </Carousel.Item>
        <Carousel.Item>
            <img className="carousel__img"
                 src={thirdImage}
                 alt="Third slide"
            />
            <Carousel.Caption className="carousel-caption text-left">
                <h3></h3>
                <p>        </p>
                <button className="carousel__button pink"></button>
            </Carousel.Caption>
        </Carousel.Item>
        <Carousel.Item>
            <img className="carousel__img"
                 src={fourthImage}
                 alt="Fourth slide"
            />
            <Carousel.Caption className="carousel-caption text-left">
                <h3></h3>
                <p>   </p>
                <button className="carousel__button violet"></button>
            </Carousel.Caption>
        </Carousel.Item>
    </Carousel>
}